$primary: #81b29a;
$primary_light: #d8e9e0;
$black: #2a1c19;
$background: #f9f9f9;
$disabled: #d9d9df;
$gray: #a2a2a2;
$boxShadow: 0 0 5px #e0e0e0;
$border: 1px solid #a2a2a2;

@mixin tool() {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 10;
    border: none;
    background: #fff;
    padding: 10px;
    cursor: pointer;
    box-shadow: $boxShadow;
    border-radius: 10px;
    svg {
        width: 15px;
        height: 15px;
    }
    &:hover {
        svg {
            filter: invert(73%) sepia(18%) saturate(503%) hue-rotate(98deg) brightness(90%) contrast(84%);
        }
    }
}

@mixin toolTL($top: 20px, $left: 20px) {
    top: $top;
    left: $left;
    @include tool();
}

@mixin toolTR($top: 20px, $right: 20px) {
    top: $top;
    right: $right;
    @include tool();
}

@mixin toolBL($bottom: 20px, $left: 20px) {
    bottom: $bottom;
    left: $left;
    @include tool();
}

@mixin toolBR($bottom: 20px, $right: 20px) {
    bottom: $bottom;
    right: $right;
    @include tool();
}
