@import '../../styles/media.scss';
@import '../../styles/color.scss';
.topTools {
    display: flex;
    align-items: center;
    position: absolute;
    z-index: 10;
    top: 20px;
    left: 50%;
    transform: translateX(-50%);
    user-select: none;
    .toolbar {
        display: flex;
        align-items: center;
        background: #fff;
        box-shadow: $boxShadow;
        border-radius: 10px;
        .tool {
            display: flex;
            align-items: center;
            position: relative;
            cursor: pointer;
            padding: 10px;
            border-radius: 10px;
            transition: 0.2s;
            margin: 0 2px;
            .keycode {
                position: absolute;
                font-size: 12px;
                right: 2px;
                font-weight: 600;
                bottom: 3px;
                transform: scale(0.9);
            }
            &:hover svg {
                filter: invert(73%) sepia(18%) saturate(503%) hue-rotate(98deg) brightness(90%) contrast(84%);
            }
            svg {
                width: 15px;
                height: 15px;
            }
            &.active {
                background-color: $primary;
                svg {
                    filter: invert(100%) sepia(4%) saturate(2%) hue-rotate(229deg) brightness(116%) contrast(100%);
                }
                .keycode {
                    color: #fff;
                }
            }
        }
    }
    .toolHint {
        white-space: nowrap;
        font-size: 12px;
        position: absolute;
        bottom: -25px;
        left: 50%;
        transform: translateX(-50%);
        color: $gray;
    }
}

@media screen and ($media_phone) {
    .topTools {
        top: 45px;
    }
}
