@import '../../styles/media.scss';
@import '../../styles/color.scss';

.rightTop {
    .download-btn {
        @include toolTR(20px, 65px);
    }
    .restore {
        @include toolTR(20px, 110px);
    }
    .save {
        @include toolTR(20px, 155px);
    }
    .delete {
        @include toolTR(20px, 200px);
    }
}

@media screen and ($media_desktop_sm) {
    .rightTop {
        .download-btn {
            @include toolTR(65px, 20px);
        }
        .restore {
            @include toolTR(110px, 20px);
        }
        .save {
            @include toolTR(155px, 20px);
        }
        .delete {
            @include toolTR(200px, 20px);
        }
    }
}

@media screen and ($media_phone) {
    .rightTop {
        .download-btn {
            @include toolBR(85px, 2.5%);
            top: unset;
        }
        .restore {
            @include toolBR(130px,  2.5%);
            top: unset;
        }
        .save {
            @include toolBR(175px,  2.5%);
            top: unset;
        }
        .delete {
            @include toolBR(220px,  2.5%);
            top: unset;
        }
    }
}
