@import '../../styles/media.scss';
@import '../../styles/color.scss';

.RWDBottomTools{
    z-index: 100;
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    align-items: center;
    justify-content: space-between;
    padding: 10px;
    margin: 10px 0;
    width: 95%;
    border-radius: 5px;
    background-color: #fff;
    box-shadow: $boxShadow;
    display: none;
    overflow-x: auto;
    .tool{
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 10px;
        border-radius: 10px;
        background-color: $background;
        transition: 0.2s;
        &:hover{
            background-color: $primary;
            color: #fff;
        }
        &.disabled{
            background-color: $disabled;
            cursor: not-allowed;
            color: #fff;
        }
        svg{
            width:20px;
            height:20px;
        }
    }
    @media screen and ($media_phone) {
        display: flex;
    }
}