@import '../../styles/media.scss';
@import '../../styles/color.scss';

#container .react-flow__node-custom .react-flow__handle-right {
    transform: none;
}

#container.wrapper {
    position: relative;
    height: 100%;
}

.dashboard-wrapper {
    width: 100%;
    height: calc(100% - 70px);
}
.logo {
    position: absolute;
    z-index: 10;
    top: 20px;
    left: 20px;
    width: 120px;
}
.react-flow__handle {
    width: 7px;
    height: 7px;
    border-radius: 50px;
    box-shadow: $boxShadow;
}
.react-flow__controls{
    border-radius: 5px;
    .react-flow__controls-zoomin{
        border-radius: 5px 5px 0 0;
    }
    .react-flow__controls-interactive{
        border-radius: 0 0 5px 5px;
    }
}
.touchdevice-flow .react-flow__handle.connecting {
    animation: bounce 1600ms infinite ease-out;
}

@media screen and ($media_phone) {
    .logo {
        top: 5px;
        left: 50%;
        width: 100px;
        transform: translateX(-50%);
    }
    .react-flow__minimap{
        display: none;
    }
    .react-flow__controls{
        bottom: 80px;
        left: 2.5%;
    }
}
