@import '../../styles/color.scss';
@import '../../styles/media.scss';

.langSelect-wrapper {
    margin-left: 26px;
    margin-right: 10px;
    position: relative;
    display: flex;
    align-items: center;
    .lang-select {
        opacity: 0;
        position: absolute;
        right: 10px;
        z-index: 20;
        top: 20px;
        .MuiSelect-select {
            margin-top: 10px;
            padding: 0;
            padding-right: 0 !important;
            width: 45px;
            height: 45px;
        }
    }
    .lang {
        cursor: pointer;
        right: 10px;
        top: 20px;
        padding: 10px;
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        z-index: 10;
        border-radius: 10px;
        width: 35px;
        height: 35px;
        background-color: #fff;
        box-shadow: $boxShadow;
    }
}
@media screen and ($media_phone) {
    .langSelect-wrapper {
        margin-left: 0;
        margin-right: 0;
        .lang-select {
            right: 2.5%;
            top: 400px;
        }
        .lang {
            right: 2.5%;
            top: 400px;
        }
    }
}
