@import '../../styles/media.scss';
@import '../../styles/color.scss';

.leftBottomTools {
    .quest {
        position: absolute;
        display: flex;
        align-items: center;
        justify-content: center;
        bottom: 140px;
        left: 18px;
        z-index: 10;
        border: none;
        background: $black;
        padding: 6px;
        cursor: pointer;
        box-shadow: $boxShadow;
        border-radius: 50%;
        svg {
            width: 10px;
            height: 10px;
        }
        @media screen and ($media_phone) {
            bottom: 195px;
            left: 3%;
        }
    }
}
