@import '../../styles/media.scss';
@import '../../styles/color.scss';

.sidebar {
    position: absolute;
    display: flex;
    align-items: center;
    flex-direction: column;
    z-index: 1000000000000;
    bottom: 50%;
    left: 10px;
    background: #fff;
    padding: 10px 15px 15px;
    border-radius: 10px;
    transition: 0.2s;
    transform: translateX(-1000px) translateY(50%);
    width: 290px;
    box-shadow: $boxShadow;
    @media screen and ($media_phone) {
        bottom: 80px;
        width: 95%;
        left: 50%;
        transform: translateX(-50%) translateY(1000px);
        max-height: 500px;
        padding: 0 15px;
    }
    &.active {
        transform: translateX(0px) translateY(50%);
        @media screen and ($media_phone) {
            transform: translateX(-50%) translateY(0);
        }
    }
    .sidebarItems {
        font-size: 14px;
        @media screen and ($media_phone) {
            width: 100%;
        }
        .sidebarItem {
            color: $black;
            width: 100%;
            margin-top: 10px;
            @media screen and ($media_phone) {
                display: flex;
                align-items: center;
                margin: 15px 0;
            }
            &.action {
                @media screen and ($media_phone) {
                    display: none;
                }
            }
            .label {
                font-weight: 600;
                margin-bottom: 5px;
                @media screen and ($media_phone) {
                    white-space: nowrap;
                    margin-right: 10px;
                    margin-bottom: 0;
                }
            }
            .colorPicker {
                display: flex;
                align-items: center;
                .colorInput {
                    cursor: pointer;
                    width: 25px;
                    height: 25px;
                    margin-right: 10px;
                }
                .colorpick-eyedropper-input-trigger {
                    width: 20px;
                    height: 20px;
                }
            }
            .borderItems {
                display: flex;
                width: 100%;
                .borderItem {
                    width: 35px;
                    height: 22px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    box-shadow: $boxShadow;
                    background: $primary_light;
                    margin-right: 15px;
                    border-radius: 5px;
                    cursor: pointer;
                    .border0 {
                        width: 25px;
                    }
                    .border1 {
                        height: 1px;
                        width: 25px;
                        background-color: $black;
                    }
                    .border2 {
                        height: 3px;
                        width: 25px;
                        background-color: $black;
                    }
                    .border3 {
                        height: 5px;
                        width: 25px;
                        background-color: $black;
                    }
                    &.active {
                        background: $primary;
                        color: #fff;
                        .border0,
                        .border1,
                        .border2,
                        .border3 {
                            background-color: #fff;
                        }
                    }
                    .type0 {
                        width: 25px;
                        border-bottom: 3px solid $black;
                    }
                    .type1 {
                        width: 25px;
                        border-bottom: 3px dotted $black;
                    }
                    .type2 {
                        width: 25px;
                        border-bottom: 3px dashed $black;
                    }
                    .type3 {
                        width: 25px;
                        border-bottom: 3px double $black;
                    }
                }
            }
            .sideTools {
                display: flex;
                align-items: center;
                .sideTool {
                    background-color: $primary_light;
                    width: 30px;
                    height: 30px;
                    display: flex;
                    align-items: center;
                    border-radius: 5px;
                    justify-content: center;
                    cursor: pointer;
                    margin: 0 10px 0 0;
                    svg {
                        width: 15px;
                        height: 15px;
                    }
                }
            }
        }
    }
}
